import React, { Component } from 'react';
import classes from './Accordion.css';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoveFolders from '../MoveFolders/MoveFolders';

import logo from './11.png';
import logo1 from './22.png';

const Accordion = props => {
  const [expanded, setExpanded] = React.useState(false);
  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.Accordion}>
      <ExpansionPanel
        expanded={expanded === 'panel1'}
        onChange={handleChange('panel1')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography>
            <img src={logo} alt={'logo'} className={classes.heading} /> Перенос
            дирректорий
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <MoveFolders />
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel
        expanded={expanded === 'panel2'}
        onChange={handleChange('panel2')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className={classes.heading}>
            <img src={logo1} alt={'logo'} className={classes.heading} /> Импорт
            файлов
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <ul>
            <li>1. Импорт ссылок</li>
            <li>2. Файл импорта</li>
            <li>3. Асана</li>
            <li>3. Очистить таблицу</li>
          </ul>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  );
};

export default Accordion;
